.innerCheck .yd-checkbox-icon {
	cursor: pointer;
}

.innerCheck .yd-checkbox-text {
	font-size: 0.28rem;
	// font-weight: bold;
	padding-left: 0.1rem;
}

.innerCheck .yd-checkbox-icon>i {
	content: "";
    position: absolute;
    top: 45%;
    left: 50%;
    border: 2px solid #55cd1a;
    border-top: 0;
    border-left: 0;
    -webkit-transform: translate(-50%,-50%) rotate(45deg) scale(0);
    transform: translate(-50%,-50%) rotate(45deg) scale(0);
}

.innerCheck .yd-checkbox>input[type=checkbox]:checked+.yd-checkbox-icon {
    border-color: #ccc;
}

