@blue: #59A5FD;
.schedules {
    .mainContent {
        padding: 0 .25rem;
        padding-bottom: .18rem;
    }

    .listDate {
        padding: .29rem 0;
    }

    .dataTxt {
        display: block;
        background: #009FFF;
        border-radius: 3px;
        color: #ffffff;
        font-size: .26rem;
        width: 2.5rem;
        height: .4rem;
        line-height: .4rem;
        text-align: center;
    }
    .dataText {
        display: block;
        background: #ccc;
        border-radius: 3px;
        color: #ffffff;
        font-size: .26rem;
        width: 2.5rem;
        height: .54rem;
        line-height: .54rem;
        text-align: center;
    }

    .sharehoto {
        width: .32rem;
        position: absolute;
        top: .18rem;
        right: .1rem;
    }

    .yd-flexbox-item-center {
        // border-bottom: 1px dashed #D4D0CB;
    }

    .pubList .yd-flexbox {
        overflow: hidden;
    }

    .status {
        color: #c82e13;
    }

    .status,
    .statusDate {
        position: absolute;
        top: 0;
        right: 0;
        line-height: .7rem;
        font-size: .24rem;
    }

    .statusDate,
    .time,
    .add {
        color: #999999;
    }

    .yd-tabbar {
        padding: 0;
    }

    .yd-icon-toView:before,
    .yd-icon-addBtn:before,
    .yd-icon-mySchedule:before {
        display: block;
        content: '';
    }

    .yd-icon-mySchedule:before {
        width: .41rem;
        height: .41rem;
        background: url(../../static/images/schedule/richeng.png) no-repeat 0 0;
        background-size: .4rem;
    }

    .yd-tabbar-active .yd-icon-mySchedule:before {
        width: .41rem;
        height: .41rem;
        background: url(../../static/images/schedule/richeng1.png) no-repeat 0 0;
        background-size: .4rem;
    }

    .yd-icon-toView:before {
        width: .5rem;
        height: .5rem;
        background: url(../../static/images/schedule/tarenricheng1.png) no-repeat 0 0;
        background-size: .5rem;
    }

    .yd-tabbar-active .yd-icon-toView:before {
        width: .5rem;
        height: .5rem;
        background: url(../../static/images/schedule/tarenricheng.png) no-repeat 0 0;
        background-size: .5rem;
    }

    .yd-tabbar-txt {
        font-size: .22rem;
    }



    .pubList .tit {
        max-width: 5rem;
    }

    .addSchedule11 .yd-icon-addBtn:before {
        width: 1.4rem;
        height: 1.4rem;
        background: url(../../static/images/schedule/s_17.png) no-repeat 0 0;
        background-size: 1.4rem;
        animation-fill-mode: forwards;
    }
    .addSchedule13 .yd-icon-addBtn:before {
        animation: rotateAni .6s;
        animation-fill-mode: forwards;
    }
    .addSchedule12 .yd-icon-addBtn:before {
        animation: rotateAniO .6s;
        animation-fill-mode: forwards;
    }
    .btnActive .yd-icon-addBtn:before {
        width: 1.4rem;
        height: 1.2rem;
        background: url(../../static/images/schedule/s_18.png) no-repeat 0 0;
        background-size: 1.4rem;
    }

    .yd-icon-addBtn {
        margin-top: -.2rem;
        z-index: 9;
    }

    .yd-grids-item span {
        font-size: .26rem;
        color: #ffffff;
    }
    .noSelect{
        display: none;
    }
    .selectBtn{
        animation: addAni .6s;
        animation-fill-mode: forwards;
        display: block;
    }
    .closeBtn{
        animation: addAniO .6s;
        animation-fill-mode: forwards;
        display: block;
    }
    .btnLeft {
        padding-left: 1.5rem;
        position: relative;
        top:.34rem;
    }

    .slotText {
        text-align: center
    }

    .btnRight {
        padding-right: 1.5rem;
    }
    .yd-tab-nav-item:not(:last-child):after {
        border-right: 0;
    }
    .yd-tab-nav .yd-tab-active {
        a{
            color: #333;
        }
        color: #333333;
    }
    .yd-tab-nav{
        a{
            color: #999;
        }
    }
    .yd-tab-nav .yd-tab-active:before {
        color: #009FFF;
        width: .5rem;
        height: .05rem;
        margin-left: -0.25rem;
        border-radius: 1000px;
    }
    .yd-tab-nav-item {
        line-height: .65rem;
        margin-bottom: 2px;
    }
    .yd-tab-nav {
        background-color: #fff;
    }
    .yd-tab-nav:after {
        border-bottom: 1px solid #F0EFED;
    }
    .yd-scrollview{
        background: red;
    }

    .search {
        background: #F8F8F7;
        padding: .11rem .13rem;
        .searchWord {
            width: 100%;
            height: .7rem;
            padding-top: .17rem;
            padding-bottom: .22rem;
            border: 0;
            box-sizing: border-box;
            text-align: center;
            color: #2c3e50;
            outline: none;
            border-radius: 2px;
            margin-right: 10px;
            transition: border-color 0.2s ease;
            background: #fff url(../../static/images/schedule/s_3.png) 47% center no-repeat;
            background-size: .26rem;
            padding-left: .56rem;
            font-size: .28rem;
            vertical-align: middle !important;
        }
    }
    .mbsc-cal-row {
        .mbsc-cal-day:first-child,.mbsc-cal-day:last-child{
            color: #59a5fd;
        }
    }
}
.schBg{
    .yd-scrollview{
        background: #f8f8f8;
    }
}
.handleTabBox{
    width: 100%;
    height: 2.11rem;
    background: #fff;
    padding-top: .36rem;
    img{
        display: inline-block;
        vertical-align: top;
        margin-right: .16rem;
        width: .33rem;
    }
    .tabTopL{
        padding-left: .46rem;
        color: #333;
        font-size: .4rem;
    }
    .tabTopR{
        color: @blue;
        font-size: .3rem;
        text-align: right;
        padding-right: .32rem;
    }
    .tabBottom{
        padding-top: .3rem;
        padding-left: .36rem;
        .tabIcon{
            width: 1.38rem;
            height: .54rem;
            line-height: .54rem;
            text-align: center;
            font-size: .32rem;
            border-radius: .54rem;
            float: left;
            color: #999;
            margin-right: .2rem;
            &.active{
                color: #fff;
                background: @blue;
                box-shadow: 0 0 .4rem 0 #d7e9fe;
            }
        }
    }
}
.addeditSchedule {
    .yd-cell-right input::-webkit-input-placeholder {
        font-size: .3rem;
    }
    .yd-cell-arrow:after{
        content: '';
        background: url(../../static/images/schedule/s_15Left.png) no-repeat center;
        background-size: .1rem;
        width: .3rem;
        height: 1rem;
        margin-left: .1rem;
    }
    .yd-cell-right input {
        font-size: .34rem;
        color: #333;
    }
    .yd-cell-left{
        font-size: .32rem;
    }
    .yd-cell-right {
        color: #999999;
        font-size: .32rem;
    }
    .yd-cell-item {
        margin: 0 .16rem;
    }
    .yd-cell-item:not(:last-child):after {
        margin-left: 0;
    }
    .yd-cell-right {
        padding-right: .07rem;
    }
    .yd-cell-item {
        padding-left: .16rem;
    }
    .dateColor {
        color: #369df4;
    }
    .yd-cell-icon img {
        width: .38rem;
        height: auto;
        vertical-align: middle;
    }
    .yd-cell-title {
        background: #fff;
        color: #333;
        padding-left: .32rem;
        padding-top: .35rem;
    }
    .yd-cell-title:after {
        border: 0;
    }
    .yd-tabbar {
        display: block!important;
        height: auto!important;
    }
}
.mbsc-cal-anim-c {
    transition: .2s ease-in-out!important;
    -webkit-transition: .2s ease-in-out!important;
    -webkit-transition: .2s ease-in-out!important;
    -moz-transition: .2s ease-in-out!important;
    -o-transition: .2s ease-in-out!important
}
.dateClose1 .mbsc-cal-anim-c {
    margin-top: -1rem;
}
.dateClose2 .mbsc-cal-anim-c {
    margin-top: -2rem;
}
.dateClose3 .mbsc-cal-anim-c {
    margin-top: -3rem;
}
.dateClose4 .mbsc-cal-anim-c {
    margin-top: -4rem;
}
.dateClose5 .mbsc-cal-anim-c {
    margin-top: -5rem;
}

.yd-tabbar:after {
    border-top: none;
}
@keyframes addAni{
    from {
        transform: scale(0);
    }
    to {
        transform: scale(1);
    }
}
@keyframes addAniO{
    from {
        transform: scale(1);
    }
    to {
        transform: scale(0);
    }
}@keyframes rotateAni{
    from {
        transform: rotateZ(0deg);
    }
    to {
        transform: rotateZ(45deg);
    }
}@keyframes rotateAniO{
    from {
        transform: rotateZ(45deg);
    }
    to {
        transform: rotateZ(0deg);
    }
}
.search {
    background: #fff;
    padding: .3rem;
    position: relative;
    &.active{
        padding: .3rem;
        .searchWord{
            width: 88%;
        }
    }
    .cancel{
        color: @blue;
        font-size: .3rem;
        position: absolute;
        right: .26rem;
        top: .11rem;
        height: .7rem;
        line-height: .7rem;
        text-align: center;
    }
    .close{
        width: .66rem;
        height: .7rem;
        text-align: center;
        position: absolute;
        top: .11rem;
        right: 1.2rem;
        line-height: .65rem;
        img{
            width: .38rem;
            vertical-align: middle;
        }
    }
    .searchWord {
        background-color: #f3f3f3;
        background-position: .27rem;
        border-radius: .08rem;
        text-align: left;
        padding-left: .66rem;
        padding-right: .6rem;
    }
}


