.appointmentTop {
    .yd-cell-item {
        padding-left: 0;
        border-bottom: 1px solid #dcdcdc;
        .yd-cell-right {
            padding-right: 0;
            .yd-textarea {
                padding: 0 0;
            }
        }
    }
