        font-size: 0.3rem;
        border-bottom: 1px solid #dcdcdc;
        .yd-back-icon:before,
        .yd-next-icon:before {
            padding-left: 0.3rem;
            font-size: 0.18rem;
        }
    }
    .yd-flexbox:nth-child(2) {
        border-bottom: none;
        padding-left: 0.65rem;
    }
}

.details .yd-flexbox:first-child {
