        font-size: 0.28rem;
        color: #999;
    }
}

.twoPart .yd-back-icon:before,
.yd-next-icon:before {
    padding-left: 0.15rem;
    font-size: 0.28rem;
    color: #999;
}

.partOne .yd-next-icon:before {
    position: absolute;
    top: 0.1rem;
    right: -0.2rem;
}

.thirdPart .yd-flexbox {
    font-size: 0.28rem;
    color: #333;
    line-height: 0.7rem;
}

.bottomBtn.yd-tabbar {
    // position: relative;
    .yd-tabbar-item {
        font-size: 0.3rem;
        color: #fff;
