        height: 0.66rem;
        line-height: 0.66rem; // position: fixed;
        border-radius: 0.1rem;
    }
    .yd-tabbar-txt {
        color: #fff;
    }
    .yd-tabbar-item:first-child {
        background-color: #61b3f6;
    }
    .yd-tabbar-item:nth-child(2) {
        background-color: #71c24a;
    }
    .yd-tabbar-item:nth-child(3) {
        background-color: #ff536f;
    }
}

.resHistory .yd-tab-panel {
    background-color: #f8f8f8;
    .mainContent {
        padding-bottom: 0;
        .yd-flexbox {
            font-size: 0.28rem;
            color: #999;
            .historyTime,
            .historyTime2 {
                height: 0.39rem;
                width: 2.67rem;
                background-color: #ffb330;
                text-align: center;
                color: #fff;
                font-size: 0.28rem;
                border-radius: 0.05rem;
            }
            .blueWord {
                color: #61b3f6;
            }
